'use strict';
import {membersData} from "/data/members.js"

const e = React.createElement;

const MembersGrid = () => {

  const RenderMembers = () => (
    membersData.map((item) => (
      <div key={item.id} class="grid-item grid grid-item--no-padding">
        <div class="member-card">
            <figure class="profile-picture">
              <img src={item.img} />
            </figure>
            <main>
            <header>
              <h5 class="color-lightgrey">{item.name}</h5>
            </header>
              <p class="paragraph color-lightgrey">{item.description}</p>
              <footer class="bg-color-primary">
                <ul>
                  <a href={item.discordUrl}><li class="icon icon__small icon--invert"><figure><img src={require('/assets/icons/discord.svg')}/></figure></li></a>
                  <a href={item.twitterUrl}><li class="icon icon__small icon--invert"><figure><img src={require('/assets/icons/twitter.svg')} /></figure></li></a>
                </ul>
              </footer>
            </main>
        </div>
      </div>
    ))
  )


    return (
      <div className="members-grid">
        <RenderMembers />
      </div>
    )
}

const domContainer = document.querySelector('#MembersGridComponent');
ReactDOM.render(e(MembersGrid), domContainer);	